@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap";
body, html {
  --maincolor: #00acdc;
  --mainfont: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 100px);
  padding: 0 50px;
  display: flex;
}

.headerimg {
  width: 40px;
  height: 40px;
}

.navlist {
  flex-direction: row;
  height: 100%;
  padding: 0;
  list-style: none;
  display: flex;
}

.headtext {
  font-family: var(--mainfont);
  color: #000;
  text-transform: uppercase;
  margin: auto auto auto 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
}

.headtext:last-of-type {
  margin-right: 0 !important;
}

.headtext:first-of-type {
  margin-left: 0 !important;
}

.headtext:hover {
  color: var(--maincolor);
  cursor: pointer;
}

.is-active {
  position: relative;
  color: var(--maincolor) !important;
}

.is-active:after {
  content: "";
  background-color: var(--maincolor);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

